"use client"

import Image from "next/image"
import Link from "next/link"
import { motion } from "framer-motion"

export default function AboutPreview() {
  return (
    <section className="py-24 bg-gradient-to-b from-white to-slate-50">
      <div className="max-w-7xl mx-auto px-6">

        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-20"
        >
          <h2 className="text-4xl md:text-5xl font-bold text-slate-900 mb-4">
            About Venturemond
          </h2>
          <p className="text-slate-600 text-lg max-w-2xl mx-auto">
            Transforming ideas into digital experiences since 2020
          </p>
        </motion.div>

        {/* Main Content Grid */}
        <div className="grid md:grid-cols-2 gap-16 items-center mb-20">

          {/* Text Content */}
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7 }}
          >
            <div className="space-y-6">
              <h3 className="text-3xl font-bold text-slate-900">
                We Create Digital Magic
              </h3>

              <p className="text-slate-600 text-lg leading-relaxed">
                We are a digital agency passionate about shaping experiences that help brands grow. 
                Our team blends creativity, strategy, and engineering to build products users love.
              </p>

              <p className="text-slate-600 leading-relaxed">
                From startups to enterprises, we've partnered with ambitious teams to craft 
                exceptional digital solutions. Our approach combines data-driven insights with 
                innovative design thinking to deliver results that matter.
              </p>

              {/* Stats */}
              <div className="grid grid-cols-3 gap-6 py-8">
                <div>
                  <div className="text-4xl font-bold text-green-600 mb-2">150+</div>
                  <div className="text-sm text-slate-600">Projects</div>
                </div>
                <div>
                  <div className="text-4xl font-bold text-green-600 mb-2">50+</div>
                  <div className="text-sm text-slate-600">Clients</div>
                </div>
                <div>
                  <div className="text-4xl font-bold text-green-600 mb-2">5+</div>
                  <div className="text-sm text-slate-600">Years</div>
                </div>
              </div>

              <Link
                href="/about"
                className="inline-flex items-center gap-2 px-8 py-4 bg-green-600 text-white font-semibold rounded-lg hover:bg-green-700 transition-all duration-300 shadow-lg hover:shadow-xl hover:-translate-y-1"
              >
                Learn More About Us
                <svg 
                  className="w-5 h-5" 
                  fill="none" 
                  stroke="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path 
                    strokeLinecap="round" 
                    strokeLinejoin="round" 
                    strokeWidth={2} 
                    d="M17 8l4 4m0 0l-4 4m4-4H3" 
                  />
                </svg>
              </Link>
            </div>
          </motion.div>

          {/* Image Section with Floating Animation */}
          <motion.div
            initial={{ opacity: 0, x: 50 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7 }}
            className="relative"
          >
            {/* Main Image */}
            <motion.div
              animate={{ y: [0, -20, 0] }}
              transition={{ duration: 5, repeat: Infinity, ease: "easeInOut" }}
              className="relative h-96 w-full rounded-2xl overflow-hidden shadow-2xl"
            >
              <Image
                src="/images/about/about1.jpg"
                alt="About Venturemond"
                fill
                className="object-cover"
              />
              {/* Green Overlay Gradient */}
              <div className="absolute inset-0 bg-gradient-to-tr from-green-600/20 to-transparent"></div>
            </motion.div>

            {/* Floating Accent Card */}
            <motion.div
              animate={{ y: [0, 15, 0] }}
              transition={{ duration: 4, repeat: Infinity, ease: "easeInOut", delay: 0.5 }}
              className="absolute -bottom-8 -left-8 bg-white p-6 rounded-xl shadow-xl border border-slate-200"
            >
              <div className="flex items-center gap-4">
                <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center">
                  <span className="text-2xl">üèÜ</span>
                </div>
                <div>
                  <div className="text-2xl font-bold text-slate-900">98%</div>
                  <div className="text-sm text-slate-600">Satisfaction</div>
                </div>
              </div>
            </motion.div>

            {/* Decorative Circles */}
            <motion.div
              animate={{ scale: [1, 1.2, 1], opacity: [0.3, 0.5, 0.3] }}
              transition={{ duration: 4, repeat: Infinity }}
              className="absolute -top-10 -right-10 w-40 h-40 bg-green-200 rounded-full blur-3xl"
            />
            <motion.div
              animate={{ scale: [1, 1.3, 1], opacity: [0.2, 0.4, 0.2] }}
              transition={{ duration: 5, repeat: Infinity, delay: 1 }}
              className="absolute -bottom-10 -left-10 w-32 h-32 bg-blue-200 rounded-full blur-3xl"
            />
          </motion.div>

        </div>

        {/* Our Values/Approach Section */}
        <motion.div
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.7 }}
        >
          <h3 className="text-3xl font-bold text-slate-900 text-center mb-12">
            Our Approach
          </h3>

          <div className="grid md:grid-cols-3 gap-8">
            {[
              {
                icon: "üí°",
                title: "Innovation First",
                description: "We leverage cutting-edge technologies and creative thinking to deliver solutions that push boundaries."
              },
              {
                icon: "üéØ",
                title: "Results Driven",
                description: "Every project is measured by real business impact. We focus on metrics that matter to your success."
              },
              {
                icon: "ü§ù",
                title: "Partnership Mindset",
                description: "Your success is our success. We collaborate closely to ensure we're aligned with your vision and goals."
              }
            ].map((item, idx) => (
              <motion.div
                key={item.title}
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.6, delay: idx * 0.15 }}
                whileHover={{ y: -10 }}
                className="bg-white p-8 rounded-2xl shadow-md hover:shadow-xl transition-all duration-300 border border-slate-200"
              >
                <div className="text-5xl mb-4">{item.icon}</div>
                <h4 className="text-xl font-bold text-slate-900 mb-3">
                  {item.title}
                </h4>
                <p className="text-slate-600 leading-relaxed">
                  {item.description}
                </p>
              </motion.div>
            ))}
          </div>
        </motion.div>

        {/* Team Highlight (Optional) */}
        <motion.div
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.7 }}
          className="mt-20 bg-gradient-to-r from-green-50 to-blue-50 rounded-3xl p-12 text-center"
        >
          <h3 className="text-3xl font-bold text-slate-900 mb-4">
            Meet Our Talented Team
          </h3>
          <p className="text-slate-600 text-lg mb-8 max-w-2xl mx-auto">
            A diverse group of designers, developers, and strategists united by a passion for excellence.
          </p>
          <Link
            href="/about#team"
            className="inline-flex items-center gap-2 px-6 py-3 bg-white text-green-600 font-semibold rounded-lg hover:shadow-lg transition-all duration-300 border-2 border-green-600 hover:bg-green-600 hover:text-white"
          >
            View Team
          </Link>
        </motion.div>

      </div>
    </section>
  )
}
