"use client"

import { motion, AnimatePresence } from "framer-motion"
import { useState, useEffect } from "react"
import Image from "next/image"

export default function Services() {
  const [currentIndex, setCurrentIndex] = useState(0)

  const items = [
    {
      title: "Web Development",
      body: "Next.js, React, Node.js, API design",
      img: "/images/services/web.png",
      description: "Building scalable, performant web applications with modern frameworks and best practices."
    },
    {
      title: "UI/UX Design",
      body: "Design systems, prototyping, research",
      img: "/images/services/design.png",
      description: "Creating intuitive, user-centered experiences that drive engagement and conversion."
    },
    {
      title: "Brand Strategy",
      body: "Identity, messaging, positioning",
      img: "/images/services/brand.png",
      description: "Developing cohesive brand identities that resonate with your target audience."
    }
  ]

  // Auto-rotate every 3 seconds
  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % items.length)
    }, 3000)

    return () => clearInterval(timer)
  }, [items.length])

  const goToSlide = (index) => {
    setCurrentIndex(index)
  }

  return (
    <section id="services" className="py-24 bg-white relative overflow-hidden">
      
      {/* Subtle Background Pattern */}
      <div className="absolute inset-0 opacity-[0.02]">
        <div className="absolute inset-0 bg-[linear-gradient(to_right,#1D3557_1px,transparent_1px),linear-gradient(to_bottom,#1D3557_1px,transparent_1px)] bg-[size:4rem_4rem]"></div>
      </div>

      <div className="max-w-6xl mx-auto px-6 relative z-10">
        
        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl font-bold text-slate-900 mb-4">
            Our Services
          </h2>
          <p className="text-slate-600 text-lg max-w-2xl mx-auto">
            Comprehensive solutions tailored to your business needs
          </p>
        </motion.div>

        {/* Carousel Container */}
        <div className="relative max-w-4xl mx-auto">
          
          {/* Slide Content */}
          <div className="min-h-[500px] flex items-center justify-center">
            <AnimatePresence mode="wait">
              <motion.div
                key={currentIndex}
                initial={{ opacity: 0, x: 100 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: -100 }}
                transition={{ duration: 0.5, ease: "easeInOut" }}
                className="w-full"
              >
                <div className="bg-gradient-to-br from-slate-50 to-white border-2 border-slate-200 rounded-3xl p-12 shadow-xl">
                  
                  {/* Service Icon */}
                  <div className="flex justify-center mb-8">
                    <div className="w-32 h-32 bg-blue-50 rounded-2xl flex items-center justify-center border border-blue-100">
                      <Image
                        src={items[currentIndex].img}
                        alt={items[currentIndex].title}
                        width={80}
                        height={80}
                        className="object-contain"
                      />
                    </div>
                  </div>

                  {/* Service Title */}
                  <h3 className="text-3xl md:text-4xl font-bold text-slate-900 text-center mb-4">
                    {items[currentIndex].title}
                  </h3>

                  {/* Service Subtitle */}
                  <p className="text-blue-600 font-semibold text-center text-lg mb-6">
                    {items[currentIndex].body}
                  </p>

                  {/* Service Description */}
                  <p className="text-slate-600 text-center text-lg max-w-2xl mx-auto leading-relaxed">
                    {items[currentIndex].description}
                  </p>

                </div>
              </motion.div>
            </AnimatePresence>
          </div>

          {/* Navigation Dots */}
          <div className="flex justify-center items-center gap-3 mt-10">
            {items.map((_, index) => (
              <button
                key={index}
                onClick={() => goToSlide(index)}
                className={`transition-all duration-300 rounded-full ${
                  index === currentIndex
                    ? "w-12 h-3 bg-blue-600"
                    : "w-3 h-3 bg-slate-300 hover:bg-slate-400"
                }`}
                aria-label={`Go to slide ${index + 1}`}
              />
            ))}
          </div>

          {/* Progress Bar */}
          <div className="mt-8 max-w-md mx-auto">
            <div className="h-1 bg-slate-200 rounded-full overflow-hidden">
              <motion.div
                key={currentIndex}
                initial={{ width: "0%" }}
                animate={{ width: "100%" }}
                transition={{ duration: 3, ease: "linear" }}
                className="h-full bg-blue-600"
              />
            </div>
          </div>

        </div>

        {/* All Services Grid */}
        <div className="mt-20 grid grid-cols-1 md:grid-cols-3 gap-6">
          {items.map((item, index) => (
            <motion.button
              key={item.title}
              onClick={() => goToSlide(index)}
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              className={`p-6 rounded-xl border-2 transition-all duration-300 text-left ${
                index === currentIndex
                  ? "border-blue-600 bg-blue-50 shadow-lg"
                  : "border-slate-200 bg-white hover:border-slate-300 hover:shadow-md"
              }`}
            >
              <div className="flex items-center gap-4 mb-3">
                <div className="w-12 h-12 bg-slate-100 rounded-lg flex items-center justify-center">
                  <Image
                    src={item.img}
                    alt={item.title}
                    width={28}
                    height={28}
                    className="object-contain"
                  />
                </div>
                <h4 className="text-lg font-semibold text-slate-900">{item.title}</h4>
              </div>
              <p className="text-sm text-slate-600">{item.body}</p>
            </motion.button>
          ))}
        </div>

      </div>
    </section>
  )
}
